@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,900;1,300;1,600&display=swap");

:root {
  --bg-card: gray;
  --border-clr: #43cc47;
  --border-clr-alter: ;

  --transition: all 700ms ease-in-out 25ms;
}

body {
  max-width: 960px;
  margin: auto;
  font-family: "Poppins", sans-serif;
  line-height: 1.7;
  position: relative;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

a {
  text-decoration: none;
  border-radius: 1rem;
  padding: 0.5rem;
  color: rgb(0, 96, 99);
  transition: var(--transition);
}

a:hover {
  color: rgb(152, 0, 139);
}
