.animA {
  position: absolute;
  top: 20vh;
  z-index: 5;
}

.animB {
  position: absolute;
  z-index: 5;
  top: 29vh;
}

.animC {
  z-index: 5;
  position: absolute;
  font-size: 3rem;
  left: 5vh;
}

.header_animation {
  color: rgb(255, 255, 255);
}

header {
  margin-bottom: 1rem;
  min-height: 100vh;
  background-image: url("../../assets/img/header-img.jpg");
  background-color: wheat;
  background-repeat: no-repeat;
  background-size: cover;
  background-position-x: right;
}

.hireMeBtn {
  position: absolute;
  left: 5vh;
  top: 84vh;
  border: none;
  background-color: rgba(115, 138, 0, 0.728);
  transition-duration: 0.4s;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 1rem;
  font-size: 3rem;
  color: white;
  padding: 0.5rem;
}

.hireMeBtn:hover {
  color: rgb(255, 255, 255);
  background-color: rgba(0, 213, 25, 0.468);
  border-color: rgba(252, 252, 252, 0.297);
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

@media screen and (max-width: 400px) {
  .header_animation {
    writing-mode: vertical-rl;
    text-orientation: mixed;
  }
}
